.c{
    height: 100vh;
    position: relative;
}
.c-bg{
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
}
.c-wrapper{
    padding: 50px;
    display: flex;
}
.c-left{
    flex:1;
}
.c-right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.c-title{
    font-size: 100px;
    width: 80%;
}

.c-info{
    display: flex;
    flex-direction: column;
}
.c-info-iten{
    font-size: 30px;
    height: 20px;
    display: flex;
    align-items: center;
    margin: 40px 0px ;
    
    
}
.k{
 font-size: 60px;
}
.c-desc{
    font-weight: 200;
}
form{
    margin-top: 20px;

}
input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button{
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
    cursor: pointer;
}
@media screen and (max-width :480px){
    .c-wrapper{
        flex-direction: column;
    }
    .c-info-iten{
        font-size: 15px;
        width: 20px;
        margin: 20px 0px;
    }
    .c-title{
        font-size: 50px;
        width: 100%;
        margin-top: 10px;
    }
    input{
        margin:10px 0px;
    }
    textarea{
        height: 60px;
    }
    button{
        padding: 15px;
    }
    .c-desc{
       margin-top: 20px;
    }
}